<template>
    <div>
      <div class="ivu-table ivu-table-default ivu-table-border">
        <div class="ivu-table-body">
          <table cellspacing="0" cellpadding="0" border="0"  style="width: 100%">
            <thead>
            <tr class="ivu-table-row">
              <th v-for="(r,index) in config.columns" :key="index" :style="{width: r.width + 'px',backgroundColor: bgColor}" class="ivu-table-column-center"  >
                <div class="ivu-table-cell ivu-columns-center"  v-if="r.type == 'index'"  >{{r.label}}</div>
                <div class="ivu-table-cell  ivu-columns-center" v-else-if="r.type == 'selection'"  ><input type="checkbox" /></div>
                <div class="ivu-table-cell ivu-columns-center"  v-else-if="r.type == 'link'"  >{{ r.label }}</div>
                <div class="ivu-table-cell ivu-columns-center"  v-else-if="r.type == 'button'"  >{{ r.label }}</div>
                <div class="ivu-table-cell ivu-columns-center"  v-else  >{{ r.label }}</div>
              </th>
            </tr>
            </thead>
            <tbody class="ivu-table-tbody" >
            <template>
                <tr class="ivu-table-row"  v-for="(item,index) in tableData" :key="index">
                  <td  v-for="(r,k) in config.columns" :key="k" >
                    <!--序号-->
                    <div class="ivu-table-cell ivu-columns-center" v-if="r.type == 'index'"  >{{ index+1 }}</div>
                    <!--多选-->
                    <div class="ivu-table-cell ivu-columns-center" v-else-if="r.type == 'selection'"><input type="checkbox" /></div>
                    <!--链接-->
                    <div class="ivu-table-cell ivu-columns-center" v-else-if="r.type == 'link'" @click="r.action(item)">{{ getCellText(r, item)}}</div>
                    <!--操作-->
                    <div class="ivu-table-cell ivu-columns-center" style="padding: 10px"  v-else-if="r.type == 'button'">
                      <template v-for="(opt, idx) in r.buttons">
                        <template v-if="opt.type=='text'">
                          <!--按钮列出现文本情况（例：已过期）-->
                          <span v-if="checkIsValid(r, item, opt)" :class="opt.class" :key="index + '' + idx">{{opt.label}}</span>
                        </template>
                        <template v-else>
                          <!--按钮-->
                          <tb-button
                            v-if="checkIsValid(r, item, opt)"
                            :key="index + '' + idx"
                            :type="opt.type"
                            :text="opt.label"
                            style="margin-left: 10px;cursor: pointer;margin-top: 10px"
                            :disabled="disabledStatus(r, item, opt)"
                            @click="handleRowEvent(opt.action,item)"
                          ></tb-button>
                        </template>
                      </template>
                    </div>
                    <!--普通文本-->
                    <div class="ivu-table-cell ivu-columns-center" v-else>
                      <span>{{ getCellText(r,item) }}</span>
                    </div>
                  </td>
                </tr>
            </template>
            </tbody>
          </table>
          <div v-show="!tableData.length" >
            <p style="text-align: center;line-height: 50px;width: 100%"><span>暂无数据</span></p>
          </div>
          <div v-show="!tableData.length" class="ivu-line"></div>
        </div>
      </div>
    </div>
</template>

<script>
import { dateFormat } from '@/utils/utils'
import TbButton from './tb-button'
export default {
  name: 'js-table',
  props: {
    bgColor: String,
    tableData: {
      type: Array,
      default: function () {
        return []
      }
    },
    config: {
      type: Object,
      default: function () {
        return {
          columns: [
            {
              label: '', // 列名
              align: 'center', // 对齐方式：left(default),center,right
              width: '', // 列宽
              field: '', // 列对应的数据源名
              format: {}, // 格式化选项或函数: 直接填写code，用于码表数据；// 时间{type:'time'};日期{type:'date'};特殊类型：{type:'fmt', idKey:'id', nameKey: 'name'}
              type: 'text', // 文本：text(default)， 超链接:link， 按钮: button， 开关: swich， 序号: index
              action: (row) => { }, // type=link、swich时的触发函数
              buttons: [
                {
                  authKey: '', // 权限按钮ID
                  label: '', // 按钮名称
                  icon: '', // 按钮上的icon
                  checkValid: (column, row, opt) => { }, // 是否显示的扩展检查函数
                  action: (row) => { } // 触发函数
                }
              ] // type='options'时按钮的配置项
            }
          ]
        }
      }
    }
  },
  components: {
    TbButton
  },
  data () {
    return {

    }
  },
  methods: {
    getCellText (column, row) {
      if (column.linkLabel) return column.linkLabel
      const val = row[column.field]
      if (column.format) {
        if (typeof (column.format) === 'function') {
          return column.format(val, row, column) // 字段回显
        } else if (typeof (column.format) === 'object') {
          if (column.format.type === 'time') {
            return dateFormat(val, 'yyyy-MM-dd HH:mm:ss')
          } else if (column.format.type === 'date') {
            return dateFormat(val, 'yyyy-MM-dd')
          }
          // else if (column.format.type === 'fmt') {
          //   return this.commonFormat(column.format, val, column.format.idKey, column.format.nameKey)
          // }
        }
        // else if (typeof (column.format) === 'string') {
        //   return this.commonFormat(column.format, val)
        // }
      }
      return val
    },
    checkIsValid (column, row, opt) {
      if (opt.checkValid) {
        return opt.checkValid(column, row, opt)
      }
      return true
    },
    disabledStatus (column, row, opt) {
      if (opt.disabled) {
        return opt.disabled(row)
      }
      return false
    },
    // cell内容按钮或链接的事件，传递回父级组件处理
    handleRowEvent (action, row) {
      if (typeof action === 'function') {
        action(row)
      } else {
        this.$emit('rowEvent', action, row)
      }
    }
  }
}
</script>

<style scoped>
  .ivu-table-cell{
    padding-left: 8px;
    padding-right: 8px;
  }

  .ivu-table-cell .table-show-copy{
    display: none;
  }

  .ivu-table-cell:hover .table-show-copy{
    display: inline-block;
  }
  .ivu-columns-center {
   text-align: center;
  }

  .demo-spin-col[data-v-156234d4] {
    position: inherit;
  }
</style>
