<template>
  <Layout  id="TalClassAttendance" style="width: 100%;" class="gary">
    <div class="container" style="margin-top: 15px">
      <Form ref="form" :model="formData" :rules="ruleForm"  label-position="right" :label-width="90" inline class="ivu-common-shadow">
        <div class="class_top">
          <FormItem  prop="cityCode" label="城市">
            <Select
              v-model="formData.cityCode"
              filterable
              style="width: 120px;"
              @on-change="cityYearChange">
              <Option v-for="(option,index) in cityWithoutAllCity" :value="option.id" :key="index">{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem  prop="year" label="年份">
            <Select v-model="formData.year" style="width: 120px;" @on-change="cityYearChange">
              <Option v-for="(item,index) in yearList" :value="item" :key="index">{{ item }}</Option>
            </Select>
          </FormItem>
          <FormItem  prop="termId"  label="学期">
            <Select v-model="formData.termId" style="width: 120px;" @on-change="termChange" clearable>
              <Option v-for="(item,index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="gradeTypeId" label="学部">
            <Select v-model="formData.gradeTypeId" style="width: 120px;" @on-change="gradeTypeChange" clearable>
              <Option v-for="(item,index) in gradeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="年级">
            <Select v-model="formData.gradeId" style="width: 120px;" @on-change="gradeChange" clearable>
              <Option v-for="(item,index) in gradeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="学科">
            <Select v-model="formData.subjectId" style="width: 120px;" @on-change="subjectChange" clearable>
              <Option v-for="(item,index) in subjectList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="班次">
            <Select
              v-model="formData.levelId"
              clearable
              filterable
              style="width: 120px;">
              <Option v-for="(option,index) in levelList" :value="option.id" :key="index">{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="学员编号">
            <Input clearable placeholder="请输入" style="width: 120px" v-model.trim="formData.studentCard"/>
          </FormItem>
          <FormItem label="学员姓名">
            <Input clearable placeholder="请输入" style="width: 120px" v-model.trim="formData.studentName"/>
          </FormItem>
          <FormItem label="老师姓名">
            <Select
              v-model="formData.teacherId"
              filterable
              clearable
              remote
              @on-change="optionChange"
              :remote-method="getTeacher"
              @on-clear="clearTeacher"
              label-in-value
              style="width: 120px;"
              :loading="loading1">
              <Option v-for="option in teacherNameList" :value="option.teaId" :key="option.teaId">{{option.sysName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="班级名称">
            <Input clearable placeholder="请输入" style="width: 120px" v-model.trim="formData.className"/>
          </FormItem>
          <FormItem label="课次">
            <InputNumber :max="100" :min="0" style="width: 120px" v-model="formData.classNum"/>
          </FormItem>
          <FormItem label="是否回访">
            <Select v-model="formData.isReturnVisit" clearable style="width:120px">
              <Option v-for="(item,index) in replyList" :value="item.value" :key="index">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="areaId" label="区域">
            <Select v-model="formData.areaId" @on-change="changeArea" clearable style="width:120px" filterable>
              <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="serviceCenterId" label="服务中心" >
            <Select v-model="formData.serviceCenterId" @on-change="changeService" clearable style="width:120px" filterable>
              <Option v-for="(item,index) in serviceCenterList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="教学点">
            <Select v-model="formData.venueId" clearable style="width:120px" filterable>
              <Option v-for="(item,index) in venueList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="classDate" label="上课日期">
            <DatePicker style="width: 120px;" class="e-input" :editable="false" v-model="formData.classDate" @on-change="changeClassDate" type="date" format="yyyy-MM-dd"></DatePicker>
          </FormItem>
          <FormItem label="自动回访">
            <Select v-model="formData.autoCallStatus" clearable style="width:120px">
              <Option v-for="(item,index) in visitStatusList" :value="item.value" :key="index">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem label="上课开始时间">
            <TimePicker v-model="intervalTime" format="HH:mm:ss" type="timerange" placement="bottom-end" @on-change="changeDateTime"  style="width: 168px"></TimePicker>
          </FormItem>
          <div style="text-align: right">
            <FormItem>
              <Button type="primary" style="margin-right: 10px;"  @click="search('form')">查询</Button>
              <Button style="margin-right: 10px;" @click="resetField">重置</Button>
            </FormItem>
          </div>
         </div>
      </Form>
      <div style="margin-top: 9px">
        <Table
          style="width: 100%"
          ref="selection"
          :columns="tableColumns"
          :data="tableData"
          :loading="tableLoading"></Table>
      </div>
      <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
        <div style="text-align: right;">
          <Page
            :total="pageTotal"
            :page-size="currentPageSize"
            :current="currentPageNum"
            show-elevator
            show-sizer
            show-total
            @on-page-size-change="pageSizeChange"
            @on-change="changePage"></Page>
        </div>
      </div>
      <!--考勤回访查看-->
      <Modal
        :mask-closable="false"
        v-model="studentReplyModel"
        @on-visible-change="handleVisibleChange"
        width="960"
        title="考勤回访查看">
        <div  class="demo-spin-col">
          <Divider orientation="left">基本信息</Divider>
          <Row type="flex" justify="center" class="code-row-bg ivu-row-class">
            <Col span="7">
              <span>学员编号：</span>
              <span class="ivu-content-color">
                    {{ visitDetail.studentCard }}
              </span>
            </Col>
            <Col span="7"><span>学员姓名：</span><span class="ivu-content-color"> {{ visitDetail.studentRealName }}</span></Col>
            <Col span="7"><span>班级名称：</span>
              <span class="ivu-content-color">
                <Tooltip :content="visitDetail.className ? visitDetail.className : '--'">
                    {{ visitDetail.className ? (visitDetail.className.length > 16 ? visitDetail.className.substr(0,15) + '......' : visitDetail.className) : '--' }}
                </Tooltip>
              </span>
            </Col>
          </Row>
          <Row type="flex" justify="center" class="code-row-bg ivu-row-class">
            <Col span="7"><span>服务中心：</span><span class="ivu-content-color">{{ visitDetail.serviceCenterName }}</span></Col>
            <Col span="7"><span>教学点：</span><span class="ivu-content-color">{{ visitDetail.venueName  }}</span></Col>
            <Col span="7"><span>教师姓名：</span><span class="ivu-content-color">{{ visitDetail.teacherName }}</span></Col>
          </Row>
          <Row type="flex" justify="center" class="code-row-bg ivu-row-class">
            <Col span="21">
              <span>上课时间：</span>
              <span class="ivu-content-color">
                <Tooltip :content="visitDetail.classTimeName ? visitDetail.classTimeName : '--'">
                    {{ visitDetail.classTimeName ? (visitDetail.classTimeName.length > 56 ? visitDetail.classTimeName.substr(0,15) + '......' : visitDetail.classTimeName) : '--' }}
                </Tooltip>
              </span>
            </Col>
          </Row>
          <Row type="flex" justify="center" class="code-row-bg ivu-row-class">
            <Col span="7"><span>是否调课：</span><span class="ivu-content-color">{{ visitDetail.changeCourseStatus ? changeCourseData[visitDetail.changeCourseStatus] : '--' }}</span></Col>
            <Col span="14"><span>课次：</span><span class="ivu-content-color">{{ visitDetail.curriculumNo }}</span></Col>
          </Row>
          <Row type="flex" justify="center" style="background: #E4F3FF" class="code-row-bg ivu-row-class">
            <Col span="21"><span>联系方式：</span><span class="ivu-content-color">
              第一联系人
              <tal-decode-text :encode-text="visitDetail.motherTel ? visitDetail.motherTel : ''"
                                   type="phone"
                                   :uid="uid">
              </tal-decode-text>
              <span style="display: inline-block;margin-left: 5px;margin-right: 5px"></span>
               第二联系人
              <tal-decode-text :encode-text="visitDetail.fatherTel ? visitDetail.fatherTel : ''"
                               type="phone"
                               :uid="uid">
              </tal-decode-text>
            </span>
            </Col>
          </Row>
          <Row type="flex" justify="center" class="code-row-bg ivu-row-class">
            <Col span="21">
              <span>考勤信息：</span>
              <span class="ivu-content-color">
                电子考勤&nbsp;(&nbsp;{{ visitDetail.checkInTime  ? visitDetail.checkInTime : '无'  }} )  &nbsp;  | &nbsp;
                ITS( {{ visitDetail.itsIsAbsent == 0 ? '出勤' : '缺勤' }} {{ visitDetail.itsCheckInTime ? visitDetail.itsCheckInTime : '' }} )  &nbsp;
              </span>
            </Col>
          </Row>
          <Divider orientation="left">回访记录</Divider>
          <div class="visit-info">
            <Form :label-width="134" label-position="right">
              <FormItem label="回访日期：" required >
                <DatePicker style="width: 120px;" class="e-input" :clearable="false" :editable="false" v-model="visitData.returnDate" @on-change="changeVisitDate" type="date" format="yyyy-MM-dd" />
              </FormItem>
              <FormItem label="回访备注：" required >
                <Input :maxlength="100" v-model="visitData.remark" :autosize="{minRows: 3,maxRows: 5}"  style="width: 360px;height: 60px;" type="textarea"/>
                <Button type="info" :loading="visitLoad" @click="edite"  style="margin-top: 45px;margin-left: 15px" >提交</Button>
              </FormItem>
            </Form>
          </div>
          <div style="padding:9px 60px 5px 60px">
            <p style="font-weight: bold;">历史回访记录</p>
            <js-table
              style="width: 100%;margin-top: 9px"
              :config="curriculumCols"
              :bgColor="bgColor"
              :tableData="curriculumData"></js-table>
            <div style="margin: 10px;" >
              <div style="text-align: right;">
                <Page
                  :total="visitData.pageTotal"
                  :page-size="visitData.currentPageSize"
                  :current="visitData.currentPageNum"
                  show-elevator
                  show-sizer
                  show-total
                  :page-size-opts="[5,10]"
                  @on-page-size-change="visitPageSizeChange"
                  @on-change="changeVisitPage"></Page>
              </div>
            </div>
          </div>
          <Spin v-if="spinShow" fix>
            <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
            <div>Loading</div>
          </Spin>
        </div>
        <div slot="footer">
           <Button type="info" @click="closeModel" >关闭</Button>
        </div>
      </Modal>
      <!--班级详情-->
      <tal-class-detail
        :show="classDetail.show"
        :classId="classDetail.id"
        @close="classDetail.show = false">
      </tal-class-detail>
      <tal-teacher-details
        ref="teacherRef"
        :schoolCode="teacherDetail.cityCode"
        :teacherId="teacherDetail.id"
        :type="teacherDetail.type"
        :creatorEmpNo="teacherDetail.empNo"
      ></tal-teacher-details>
    </div>
    <!-- 课次信息 -->
    <TalSchooltime ref="talSchooltime" />
  </Layout>
</template>

<script>
import { getStrLength } from '@/utils/utils'
import { dateFormat } from '@/libs/tools'
import BaseDataMixin from '@/mixins/baseData'
import ClassMixin from '@/mixins/classDetail'
import TeacherMixin from '@/mixins/teacherDetail'
import {
  queryAttendanceReturn,
  editAttendanceReturn,
  queryElectronAttendReturn
} from '@/api/attendance'
import {
  queryServiceCenterByEmpNo
} from '@/api/clazz'
import { setFrom } from '@/api/bd'
import JsTable from '@/components/jsTable/js-table'
import Cookie from 'js-cookie'
const defaultSetQuery = {
  year: new Date().getFullYear() + '', // 必填
  cityCode: '', // 必填
  termId: '',
  gradeTypeId: '',
  gradeId: '',
  subjectId: '',
  levelId: '', // 班次
  studentCard: '', // 学员编号
  studentName: '', // 学员姓名
  teacherName: '', // 教师姓名
  teacherId: '', // 老师id
  className: '', // 班级名称
  classNum: null,
  isReturnVisit: null, // 是否回访(0,未回访，1 已回访)
  autoCallStatus: null, // 回访状态(3 已接通  4 未接通)
  areaId: '',
  serviceCenterId: '',
  venueId: '', // 教学点
  classDate: '', // 上课时间
  minStartTime: '',
  maxStartTime: ''
}
const isReply = { 0: '未回访', 1: '已回访' }
const isAttendReturn = { 0: '否', 1: '是' }
export default {
  name: 'studentAttendanceReply',
  mixins: [BaseDataMixin, ClassMixin, TeacherMixin],
  components: {
    JsTable
  },
  data () {
    return {
      formData: Object.assign({}, defaultSetQuery),
      loading1: false,
      tableLoading: false,
      spinShow: false, // 历史回访
      visitLoad: false, // 提交按钮load
      studentReplyModel: false, // 考勤回访查看model
      intervalTime: [], // 时间段
      visitData: { // 学员考勤回访详情table
        returnDate: dateFormat(new Date().getTime() + 1000, 'yyyy-MM-dd'),
        visitTime: '',
        remark: '',
        currentPageNum: 1, // 当前页
        currentPageSize: 5, // 显示条数
        pageTotal: 0 // 总条数
      },
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数
      cityWithoutAllCity: [],
      allCityList: [], // 分校
      allCityMap: {}, // 分校
      termList: [], // 学期
      gradeTypeList: [], // 年部
      subjectList: [], // 学科
      gradeList: [], // 年级
      levelList: [], // 班次
      teacherNameList: [], // 教师姓名
      areaList: [], // 区域
      serviceCenterList: [], // 服务中心
      venueList: [], // 教学点
      replyList: [
        { value: 0, label: '未回访' },
        { value: 1, label: '已回访' }
      ], // 是否回访
      visitStatusList: [
        { value: 3, label: '已接通' },
        { value: 4, label: '未接通' }
      ], // 回访状态
      ruleForm: {
        cityCode: [{ required: true, message: '分校不能为空', trigger: 'change' }],
        year: [{ required: true, message: '年份不能为空', trigger: 'change' }],
        termId: [{ required: true, message: '学期不能为空', trigger: 'change' }],
        gradeTypeId: [{ required: true, message: '学部不能为空', trigger: 'change' }],
        areaId: [{ required: true, message: '地区不能为空', trigger: 'change' }],
        serviceCenterId: [{ required: true, message: '服务中心不能为空', trigger: 'change' }],
        classDate: [{ required: true, message: '上课日期不能为空', trigger: 'change' }]
      },
      tableData: [], // 表格数据
      tableColumns: [
        { title: '序号', key: 'count', align: 'center' },
        { title: '学员编号', key: 'studentCard', align: 'center' },
        { title: '学员姓名', key: 'studentName', align: 'center' },
        { title: '班级名称',
          key: 'className',
          align: 'center',
          width: 160,
          render: (h, params) => {
            return h('div', [
              h('div', {
                style: {
                  'text-align': 'right'
                }
              }, [
                h('Tag', {
                  style: {
                    'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                  },
                  props: {
                    color: 'green'
                  }
                }, '全国班')
              ]),
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.classId)
                  }
                }
              }, params.row.className)
            ])
          }
        },
        { title: '服务中心', key: 'serviceCenterName', align: 'center' },
        { title: '教学点', key: 'venueName', align: 'center' },
        { title: '教师名称',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.showTeacherDetail(this.formData.cityCode, params.row.teacherId, '0')
                }
              }
            }, params.row.teacherName)
          }
        },
        { title: '上课时间',
          key: 'classTimeName',
          width: '120',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: async () => {
                  this.showSchooltime({ cityId: this.formData.cityCode, id: params.row.classId })
                }
              }
            }, getStrLength(params.row.classtimeNames, 15, true))
          }
        },
        { title: '开课日期', key: 'startDate', align: 'center' },
        { title: '结课日期', key: 'endDate', align: 'center' },
        { title: '课次', key: 'curriculumNum', align: 'center' },
        { title: '是否有考勤',
          align: 'center',
          render: (h, params) => {
            return h('span', isAttendReturn[params.row.isAttend])
          }
        },
        { title: '是否回访',
          align: 'center',
          render: (h, params) => {
            return h('span', {
              style: {
                color: params.row.isVisit === 1 ? '#000' : 'red'
              }
            }, isReply[params.row.isVisit])
          }
        },
        { title: '自动回访',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.autoCallStatus === 3 ? '已接通' : (params.row.autoCallStatus === 4 ? '未接通' : '--'))
          }
        },
        { title: '自动回访时间', key: 'callTime', align: 'center' },
        {
          title: '操作',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'info',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.seeReply(params.row)
                  }
                }
              }, '查看')
            ])
          }
        }
      ],
      curriculumCols: {
        columns: [
          { label: '回访人', field: 'employeeName' },
          { label: '回访日期', field: 'returnDate' },
          { label: '回访备注', field: 'remark' },
          { label: '所在班级', field: 'className' },
          { label: '创建时间', width: '100', field: 'createDate' }
        ]
      },
      bgColor: '#F8F8F9', // '#FAFAFA',
      curriculumData: [], // 历史回访记录
      chooseData: {},
      visitDetail: {}, // 查看学员
      uid: '',
      changeCourseData: { 0: '否', 1: '是' }
    }
  },
  created () {
    this.getYearListHandler()
    this.getCityListHandler()
    setFrom({
      apiName: 'stuAttendanceVisitPage'
    })
  },
  watch: {
    allCityList (val) {
      if (val && val.length) {
        this.formData.cityCode = val[0].id
        this.cityYearChange(this.formData.cityCode)
      }
      this.cityWithoutAllCity = val.filter(obj => obj.id !== '1111')
    }
  },
  methods: {
    // 基本数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      let toNum = {
        cityCode: this.formData.cityCode
      }
      this.getAreaListHandler(toNum).then(res => {
        this.areaList = res
        queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        }).then(data => {
          this.formData.areaId = data.areaId
          this.changeArea(data.areaId, data.serviceCenterId)
        }).catch(err => {
          this.$Message.error('获取当前所在服务中心失败:' + err.message)
        })
      }).catch(err => {
        this.$Message.error('获取区域失败:' + err.message)
      })
    },
    changeClassDate (val) {
      this.formData.classDate = val
    },
    changeVisitDate (val) {
      this.visitData.returnDate = val
      this.visitData.visitTime = val
    },
    // 根据状态清空 学期，年部，年级，学科，班次数据
    clearList (type) {
      switch (type) {
        case 'school':
          this.termList = []
          this.formData.termId = ''
          this.gradeTypeList = []
          this.formData.gradeTypeId = ''
          this.gradeList = []
          this.formData.gradeId = ''
          this.subjectList = []
          this.formData.subjectId = ''
          this.levelList = []
          this.formData.levelId = ''
          this.formData.areaId = ''
          this.areaList = []
          this.formData.serviceCenterId = ''
          this.serviceCenterList = []
          this.formData.venueId = ''
          this.venueList = []
          break
        case 'term' :
          this.formData.gradeTypeId = ''
          this.formData.gradeId = ''
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.gradeTypeList = []
          this.gradeList = []
          this.subjectList = []
          this.levelList = []
          break
        case 'gradeType' :
          this.formData.gradeId = ''
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.gradeList = []
          this.subjectList = []
          this.levelList = []
          break
        case 'grade' :
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.subjectList = []
          this.levelList = []
          break
        case 'subject' :
          this.formData.levelId = ''
          this.levelList = []
          break
      }
    },
    cityYearChange (val) { // 改变城市
      this.clearList('school')
      if (!val) {
        return false
      }
      if (!this.formData.cityCode) {
        this.$Message.warning('请选择城市')
        return false
      }
      this.getBaseData()
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      this.getLinkageHandler(req).then(res => {
        this.termList = res
      }).catch(err => {
        this.$Message.error('获取学期失败:' + err.message)
      })
    },
    termChange (val) { // 改变学期
      this.clearList('term')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeTypeList = res
      }).catch(err => {
        this.$Message.error('获取年部失败:' + err.message)
      })
    },
    gradeTypeChange (val) { // 改变年部
      this.clearList('gradeType')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error('获取年级失败:' + err.message)
      })
    },
    gradeChange (val) { // 改变年级
      this.clearList('grade')
      if (!val) return
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      this.getLinkageHandler(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error('获取学科失败:' + err.message)
      })
    },
    subjectChange (val) { // 改变学科
      this.clearList('subject')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        subjectId: this.formData.subjectId,
        queryType: 'c'
      }
      this.getLinkageHandler(req).then(res => {
        this.levelList = res
      }).catch(err => {
        this.$Message.error('获取班次失败:' + err.message)
      })
    },
    // 主讲老师搜索
    getTeacher (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        this.teacherNameList = res.records
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },
    changeDateTime (val) {
      if (val) {
        this.formData.minStartTime = val[0]
        this.formData.maxStartTime = val[1]
      }
    },
    clearTeacher () {
      this.oldSearchName = ''
      if (this.formData.teacherId) {
        this.formData.teacherId = ''
      }
    },
    changeArea (val, serviceCenterId) { // 改变区域
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req).then(res => {
        this.serviceCenterList = res
        this.serviceCenterList.forEach((value) => {
          if (!value.address) {
            value.address = ''
          }
        })
        if (serviceCenterId) {
          this.formData.serviceCenterId = serviceCenterId
          this.changeService(serviceCenterId)
        }
      }).catch(err => {
        this.$Message.warning('获取服务中心失败:' + err.message)
      })
    },
    changeService (val) { // 改变服务中心
      this.venueList = []
      this.formData.venueId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning('获取教学点失败:' + err.message)
      })
    },
    // 查询
    search (data) {
      this.getTableData(data)
    },
    // 重置
    resetField () {
      this.formData = Object.assign({}, defaultSetQuery)
      if (this.cityWithoutAllCity) {
        this.formData.cityCode = this.cityWithoutAllCity[0].id
        this.cityYearChange(this.formData.cityCode)
      }
      this.intervalTime = []
      this.$refs['form'].resetFields()
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData('form')
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData('form')
    },
    visitPageSizeChange (size) {
      this.visitData.currentPageNum = 1
      this.visitData.currentPageSize = size
      this.getDetailData()
    },
    changeVisitPage (num) {
      this.visitData.currentPageNum = num
      this.getDetailData()
    },
    // 表格数据
    getTableData (data) {
      this.$refs[data].validate((valid) => {
        if (valid) {
          this.formData.pageSize = this.currentPageSize
          this.formData.pageNo = this.currentPageNum
          this.formData.teacherName = this.oldSearchName
          this.tableLoading = true
          queryElectronAttendReturn(this.formData).then(res => {
            if (res.records.length === 0) {
              this.tableData = []
              this.$Message.warning('暂无数据')
            }
            this.pageTotal = res.totalCount
            this.tableData = res.records
            this.tableData.forEach((item, index) => {
              item.count = index + 1
              if (!item.startDate) item.startDate = '--'
              if (!item.endDate) item.endDate = '--'
              if (!item.callTime) item.callTime = '--'
            })
            this.tableLoading = false
          }).catch(err => {
            this.tableLoading = false
            this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
          })
        }
      })
    },
    // 查看学员回访
    getDetailData () {
      this.spinShow = true
      queryAttendanceReturn({
        pageNo: this.visitData.currentPageNum,
        pageSize: this.visitData.currentPageSize,
        preAttendanceId: this.chooseData.preAttendanceId,
        cityCode: this.formData.cityCode
      }).then(res => {
        if (res.record.records.length === 0) {
          this.curriculumData = []
        }
        this.spinShow = false
        this.visitDetail = res.info
        this.curriculumData = res.record.records
        this.visitData.pageTotal = res.record.totalCount
      }).catch(err => {
        this.spinShow = false
        this.$Message.error({
          content: `获取学员考勤回访信息失败：${err.message}`,
          duration: 5
        })
      })
    },
    // 清空学员考勤回访信息
    closeModel () {
      this.studentReplyModel = false
      this.chooseData = {}
      this.visitData = {
        remark: '',
        returnDate: dateFormat(new Date().getTime() + 1000, 'yyyy-MM-dd'),
        currentPageNum: 1, // 当前页
        currentPageSize: 5, // 显示条数
        pageTotal: 0 // 总条数
      }
    },
    // 考勤回访查看model
    seeReply (data) {
      this.studentReplyModel = true
      this.chooseData = data
      this.uid = data.studentUid
      this.getDetailData()
    },
    handleVisibleChange (val) {
      if (val) {
        this.visitData.visitTime = dateFormat(new Date().getTime() + 1000, 'yyyy-MM-dd')
      }
    },
    // 提交
    edite () {
      if (!this.visitData.remark) {
        this.$Message.warning({ content: '回访备注不能为空' })
        return
      }
      this.visitLoad = true
      editAttendanceReturn({
        preAttendanceId: this.chooseData.preAttendanceId, // 预考勤id
        remark: this.visitData.remark, // 回访备注
        returnDate: this.visitData.visitTime, // 回访日期
        cityCode: this.formData.cityCode
      }).then(res => {
        this.visitLoad = false
        this.$Message.success({ content: '修改成功' })
        this.visitData.remark = ''
        this.getDetailData()
        this.getTableData('form')
      }).catch(err => {
        this.visitLoad = false
        this.$Message.error({
          content: `获取学员考勤回访信息失败：${err.message}`,
          duration: 5
        })
      })
    }
  }
}
</script>
