  }
  .class_top {
    background: #fff;
    padding-top: 24px;
    font-size: 14px;
  }
  .ivu-content-color {
    font-weight: bold;
  }
  .ivu-row-class {
    padding: 6px 0;
    margin-top: 5px;
    font-size: 12px;
  }
  .demo-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
  }
  @keyframes ani-demo-spin {
    from { transform: rotate(0deg);}
    50%  { transform: rotate(180deg);}
    to   { transform: rotate(360deg);}
  }
  .ivu-divider-horizontal.ivu-divider-with-text-left:before {
    width:3% !important;
  }
  .demo-spin-col{
    position: inherit;
  }
