export default {
  data () {
    return {
      stuDetail: {
        show: false,
        cityCode: '',
        uid: ''
      }
    }
  },
  methods: {
    showStuDetail (cityCode, uid) {
      this.stuDetail.cityCode = cityCode
      this.stuDetail.uid = uid
      this.stuDetail.show = true
    }
  }
}
