<template>
  <div class="wrapper">
    <div v-for="(item, index) in dataList" :key="index" class="class">
      <div style="text-align: right">打印时间：{{ now }}</div>
      <div class="class-name">{{item.classDetail.name}}</div>
      <div class="class-info">
        <div class="info">课次：第{{item.passedCount}}次课</div>
        <div class="info">服务中心：{{item.classDetail.serviceCenterName}}</div>
        <div class="info">教学点：{{item.classDetail.venueName}}</div>
        <div class="info">教室：{{item.classDetail.classroomName}}</div>
        <div class="info">主讲老师：{{item.classDetail.teacherNames}}</div>
        <div class="info">开课时间：{{item.classDetail.startDate ? item.classDetail.startDate.split(' ')[0] : ''}} 至 {{item.classDetail.endDate ? item.classDetail.endDate.split(' ')[0] : ''}}</div>
        <div class="info">上课时间：{{item.classDetail.classtimeNames}} <span>({{item.classDetail.classDate}})</span> </div>
        <div class="info">教师签字：</div>
      </div>
      <table border="1" width="100%" cellspacing="0" cellpadding="0" bgcolor="#ffffff" bordercolordark="#ffffff" bordercolorlight="#000000" style="border:1px solid #000;border-collapse:collapse">
        <tr>
          <td colspan="3" align="center">上课学员考勤单</td>
        </tr>
        <tr>
          <td align="center" width="60px">编号</td>
          <td align="center" width="100px">学员姓名</td>
          <td align="center">考勤</td>
        </tr>
        <tr v-for="(stu, index2) in item.classStudents" :key="index2">
          <td align="center">{{index2 + 1}}</td>
          <td align="center">{{stu.studentRealName}}</td>
          <td align="center">{{ getKaoQinText(stu) }}</td>
        </tr>
      </table>
      <div class="page-break" v-if="dataList.length != index + 1"></div>
    </div>
  </div>
</template>

<script>
import { printAttendance } from '@/api/attendance'
import { dateFormat } from '@/utils/utils'
export default {
  data () {
    return {
      dataList: [],
      now: dateFormat(new Date())
    }
  },
  methods: {
    getKaoQinText (stu) {
      let text = ''
      switch (stu.modifyType) {
        case 0:
          text = '准时上课'
          break
        case 1:
          text = '迟到'
          break
        case 2:
          text = '缺勤'
          break
        default:
          text = '无数据'
      }
      return `${text} ${stu.lastModifyDate ? stu.lastModifyDate : ''}`
    }
  },
  mounted () {
    document.documentElement.className = 'printPage'
    document.body.className = 'printPage'
    let params = {
      cityCode: this.$route.query.cityCode,
      classId: this.$route.query.classIds
    }
    if (this.$route.query.classNum) {
      params.classNum = this.$route.query.classNum
    }
    if (this.$route.query.curriculumId) {
      params.curriculumId = this.$route.query.curriculumId
    }
    printAttendance(params).then(data => {
      if (data && data.length) {
        if (data.length === 1) {
          // 一个班级多个课次
          const passedCounts = Object.keys(data[0].stringListMap)
          data = passedCounts.map(passedCount => {
            return {
              classDetail: data[0].classDetail,
              passedCount,
              classStudents: data[0].stringListMap[passedCount]
            }
          })
        } else {
          // 多个班级一个课次
          data = data.map(d => {
            const passedCounts = Object.keys(d.stringListMap) // 课次
            return {
              classDetail: d.classDetail,
              passedCount: passedCounts[0],
              classStudents: d.stringListMap[passedCounts[0]]
            }
          })
        }
        this.dataList = data
        setTimeout(() => {
          window.print()
        }, 500)
      } else {
        this.dataList = []
      }
    }).catch(err => {
      this.$Message.error({ content: '获取班级考勤信息失败：' + err.message, duration: 5 })
    })
  }
}
</script>
<style lang="less">
  .printPage {
    height: initial;
    overflow: auto!important;
  }
</style>
<style lang="less" scoped>
  .wrapper{
    /*padding-left: 20px;*/
    /*padding-right: 20px;*/
    .class {
      margin-bottom: 30px;
      .class-name {
        text-align: center;
        font-size: 16px;
        font-weight: bold;
      }
      .class-info {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10px;
        .info {
          width: 50%;
          margin-top: 10px;
        }
      }
    }
    .page-break {
      page-break-after: always;
    }
  }
</style>
