<template>
  <div class="wrapper">
    <div v-for="(clazz, index) in dataList" :key="clazz.classId" class="class">
      <div style="text-align: right">打印时间：{{ now }}</div>
      <div class="class-name">{{clazz.className}}</div>
      <div class="class-info">
        <div class="info">服务中心：{{clazz.serviceCenterName}}</div>
        <div class="info">教学点：{{clazz.venueName}}</div>
        <div class="info">教室：{{clazz.classroomName}}</div>
        <div class="info">主讲老师：{{clazz.teacherNames}}</div>
        <div class="info">辅导老师：{{clazz.tutorRealName}}</div>
        <div class="info">开课时间：{{clazz.startDate ? clazz.startDate.split(' ')[0] : ''}} 至 {{clazz.endDate ? clazz.endDate.split(' ')[0] : ''}}</div>
        <div class="info">上课时间：{{clazz.classtimeNames}}</div>
        <div class="info">教师签字：</div>
      </div>
      <table border="1" width="100%" cellspacing="0" cellpadding="0" bgcolor="#ffffff" bordercolordark="#ffffff" bordercolorlight="#000000" style="table-layout:fixed;border:1px solid #000;border-collapse:collapse">
        <tr>
          <td rowspan="2" align="center" width="40px">编号</td>
          <td rowspan="2" align="center" width="60px">学员姓名</td>
          <td :colspan="clazz.classCount" align="center" style="font-weight: bold">准时出勤：<Icon type="md-checkmark" />，迟到：<Icon type="md-close" />，缺勤：O，资料领取：Y</td>
        </tr>
        <tr>
          <td v-for="i of clazz.classCount" :key="clazz.classId +  i" align="center">{{i}}</td>
        </tr>
        <tr v-for="(stuName, index) in clazz.studentNameList" :key="index">
          <td align="center">{{index + 1}}</td>
          <td align="center">{{stuName}}</td>
          <td v-for="i of clazz.classCount" :key="clazz.classId +  i"></td>
        </tr>
      </table>
      <table border="1" width="100%" cellspacing="0" cellpadding="0" bgcolor="#ffffff" bordercolordark="#ffffff" bordercolorlight="#000000" style="table-layout:fixed;border:1px solid #000;border-collapse:collapse;margin-top: 20px">
        <tr>
          <td rowspan="2" align="center" width="100px">临时调课课次</td>
          <td align="center" style="font-weight: bold">临时调课次数由任课老师根据实际调课次数填写。最后一次课调课学员，仅发给优秀奖</td>
        </tr>
        <tr>
          <td align="center">临时调课学员名单</td>
        </tr>
        <tr v-for="i of clazz.classCount" :key="clazz.classId +  i">
          <td align="center">{{i}}</td>
          <td align="center"></td>
        </tr>
      </table>
      <div class="page-break" v-if="dataList.length != index + 1"></div>
    </div>
  </div>
</template>

<script>
import { getClassRollCallingPaper } from '@/api/clazz'
import { dateFormat } from '@/utils/utils'
export default {
  data () {
    return {
      dataList: [],
      now: dateFormat(new Date())
    }
  },
  mounted () {
    document.documentElement.className = 'printPage'
    document.body.className = 'printPage'
    getClassRollCallingPaper({
      cityCode: this.$route.query.cityCode,
      classIds: this.$route.query.classIds
    }).then(data => {
      this.dataList = data.map(c => {
        c.classCount = c.classCount > 40 ? 40 : c.classCount
        return c
      })
      setTimeout(() => {
        window.print()
      }, 500)
    }).catch(err => {
      this.$Message.error({ content: '获取班级点名册信息失败：' + err.message, duration: 5 })
    })
  }
}
</script>
<style lang="less">
  .printPage {
    height: initial;
    overflow: auto!important;
  }
</style>
<style lang="less" scoped>
  .wrapper{
    /*padding-left: 20px;*/
    /*padding-right: 20px;*/
    .class {
      margin-bottom: 30px;
      .class-name {
        text-align: center;
        font-size: 16px;
        font-weight: bold;
      }
      .class-info {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10px;
        .info {
          width: 50%;
          margin-top: 10px;
        }
      }
    }
    .page-break {
      page-break-after: always;
    }
  }
</style>
