<template>
  <Layout id="TalClassAttendance" style="width: 100%;" class="gary">
    <div class="container">
      <Form ref="form" :model="formData" :rules="ruleForm" label-position="right" :label-width="90" inline class="ivu-common-shadow" style="padding-top: 18px;background: #fff">
        <FormItem prop="cityCode" label="城市">
          <Select
            v-model="formData.cityCode"
            filterable
            style="width: 120px;"
            @on-change="cityYearChange">
            <Option v-for="(option,index) in cityWithoutAllCity" :value="option.id" :key="index">{{option.name}}</Option>
          </Select>
        </FormItem>
        <FormItem prop="year" label="年份">
          <Select v-model="formData.year" style="width: 120px;" @on-change="cityYearChange">
            <Option v-for="(item,index) in yearList" :value="item" :key="index">{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="termId" label="学期">
          <Select v-model="formData.termId" style="width: 120px;" @on-change="termChange">
            <Option v-for="(item,index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="gradeTypeId" label="年部">
          <Select v-model="formData.gradeTypeId" style="width: 120px;" @on-change="gradeTypeChange" clearable>
            <Option v-for="(item,index) in gradeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="年级">
          <Select v-model="formData.gradeId" style="width: 120px;" @on-change="gradeChange" clearable>
            <Option v-for="(item,index) in gradeList" :value="item.id" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="学科">
          <Select v-model="formData.subjectId" style="width: 120px;" @on-change="subjectChange" clearable filterable>
            <Option v-for="(item,index) in subjectList" :value="item.id" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="班次">
          <Select
            v-model="formData.levelId"
            clearable
            filterable
            style="width: 120px;">
            <Option v-for="(option,index) in levelList" :value="option.id" :key="index">{{option.name}}</Option>
          </Select>
        </FormItem>
        <FormItem label="学员编号">
          <Input clearable placeholder="请输入" style="width: 120px" v-model.trim="formData.studentCard"/>
        </FormItem>
        <FormItem label="学员姓名">
          <Input clearable placeholder="请输入" style="width: 120px" v-model.trim="formData.studentName"/>
        </FormItem>
        <FormItem label="老师姓名">
          <Select
            v-model="formData.teacherId"
            filterable
            clearable
            remote
            @on-change="optionChange"
            :remote-method="getTeacher"
            @on-clear="clearTeacher"
            label-in-value
            style="width: 120px;"
            :loading="loading1">
            <Option v-for="option in teacherNameList" :value="option.teaId" :key="option.teaId">{{option.sysName}}</Option>
          </Select>
        </FormItem>
        <FormItem prop="areaId" label="区域">
          <Select v-model="formData.areaId" @on-change="changeArea" style="width:120px" filterable>
            <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem prop="serviceCenterId" label="服务中心">
          <Select v-model="formData.serviceCenterId" @on-change="changeService" style="width:120px" filterable>
            <Option v-for="(item,index) in serviceCenterList" :value="item.schoolId" :key="index">{{ item.name }}
            </Option>
          </Select>
        </FormItem>
        <FormItem label="教学点">
          <Select v-model="formData.venueId" clearable style="width:120px" filterable>
            <Option v-for="(item,index) in venueList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="上课时间类型">
          <Select v-model="formData.classtimeTimeClassify" clearable style="width:120px">
            <Option v-for="(item,index) in classtimeTypeList" :value="item.name" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="上课时间">
          <Select v-model="formData.classtimeTimePeriod" clearable style="width:120px">
            <Option v-for="(item,index) in classtimeList" :value="item.name" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <div style="text-align: right;background: #ffffff">
          <FormItem>
            <Button type="primary" style="margin-right: 10px;" @click="search('form')">查询班级</Button>
            <Button style="margin-right: 10px;" @click="resetField">重置</Button>
          </FormItem>
        </div>
      </Form>
      <div style="margin-top: 9px">
        <Table
          style="width: 100%"
          ref="selection"
          :columns="tableColumns"
          :data="tableData"
          :loading="tableLoading">
          <template slot-scope="{ row }" slot="operation">
            <a style="margin-right: 10px" @click="showAttendance(row, false)">查看学员考勤单</a>
            <a @click="showAttendance(row, true)">修改考勤</a>
          </template>
          <template slot-scope="{ row }"  slot="isReceiptBooks">
            {{ row.isReceiptBooks === 0 ? '未领取' : '已领取'}}
          </template>
        </Table>
      </div>
      <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
        <div style="float: right;">
          <Page
            :total="pageTotal"
            :page-size="currentPageSize"
            :current="currentPageNum"
            show-elevator
            show-sizer
            show-total
            @on-page-size-change="pageSizeChange"
            @on-change="changePage"></Page>
        </div>
      </div>
    </div>
    <tal-class-detail
      :show="classDetail.show"
      :classId="classDetail.id"
      @close="classDetail.show = false">
    </tal-class-detail>
    <tal-teacher-details
      ref="teacherRef"
      :schoolCode="teacherDetail.cityCode"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type"
      :creatorEmpNo="teacherDetail.empNo"
    ></tal-teacher-details>
    <Modal
      :mask-closable="false"
      title="学员详情"
      v-model="stuDetail.show"
      :footer-hide="true"
      width="960px">
      <!--学员详情-->
      <tal-stu-details
        :cityCode="stuDetail.cityCode"
        :uid="stuDetail.uid"
        v-if="stuDetail.show"
        @close="stuDetail.show = false">
      </tal-stu-details>
    </Modal>
    <Modal
      :mask-closable="false"
      title="考勤查看"
      v-model="attendanceShow"
      :footer-hide="true"
      width="960px">
      <div style="margin-bottom: 10px">
        <Row style="margin-bottom: 10px" :gutter="10">
          <Col span="6">学员编号：<span style="color: #2d8cf0">{{row.studentCard}}</span></Col>
          <Col span="6">学员姓名：<span style="color: #2d8cf0">{{row.studentName}}</span></Col>
          <Col span="6">班级：<span style="color: #2d8cf0">{{row.className}}</span></Col>
          <Col span="6">学科：<span style="color: #2d8cf0">{{row.subjectNames}}</span></Col>
        </Row>
        <Row :gutter="10">
          <Col span="6">上课时间：<span style="color: #2d8cf0">{{row.classtimeNames}}</span></Col>
          <Col span="6">开课日期：<span style="color: #2d8cf0">{{row.startDate}}</span></Col>
          <Col span="6">结课日期：<span style="color: #2d8cf0">{{row.endDate}}</span></Col>
          <Col span="3">总课次：<span style="color: #2d8cf0">{{row.classCount}}</span></Col>
          <Col span="3">已上课次：<span style="color: #2d8cf0">{{row.passedCount}}</span></Col>
        </Row>
      </div>
      <Table
          style="width: 100%"
          :columns="attendanceCols"
          :data="attendanceData">
        </Table>
    </Modal>
    <Modal
      :mask-closable="false"
      v-model="editAttendanceShow"
      width="1200"
      title="修改考勤记录">
      <div style="margin-bottom: 10px">
        <Row style="margin-bottom: 10px" :gutter="10">
          <Col span="6">学员编号：<span style="color: #2d8cf0">{{row.studentCard}}</span></Col>
          <Col span="6">学员姓名：<span style="color: #2d8cf0">{{row.studentName}}</span></Col>
          <Col span="6">班级：<span style="color: #2d8cf0">{{row.className}}</span></Col>
          <Col span="6">学科：<span style="color: #2d8cf0">{{row.subjectNames}}</span></Col>
        </Row>
        <Row :gutter="10">
          <Col span="6">上课时间：<span style="color: #2d8cf0">{{row.classtimeNames}}</span></Col>
          <Col span="6">开课日期：<span style="color: #2d8cf0">{{row.startDate}}</span></Col>
          <Col span="6">结课日期：<span style="color: #2d8cf0">{{row.endDate}}</span></Col>
          <Col span="3">总课次：<span style="color: #2d8cf0">{{row.classCount}}</span></Col>
          <Col span="3">已上课次：<span style="color: #2d8cf0">{{row.passedCount}}</span></Col>
        </Row>
      </div>
      <div class="ivu-table ivu-table-default ivu-table-wrapper">
        <Spin size="large" fix v-if="attendLoading"></Spin>
        <div class="ivu-table-body">
          <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
            <thead>
            <tr class="ivu-table-row">
              <th class="ivu-table-column-center"><div class="ivu-table-cell">课次</div></th>
              <th class="ivu-table-column-center"><div class="ivu-table-cell">上课日期</div></th>
              <th class="ivu-table-column-center" width="200px"><div class="ivu-table-cell">考勤状态</div></th>
              <th class="ivu-table-column-center"><div class="ivu-table-cell">考勤时间</div></th>
              <th class="ivu-table-column-center"><div class="ivu-table-cell">最后修改人</div></th>
              <th class="ivu-table-column-center"><div class="ivu-table-cell">修改时间</div></th>
              <th class="ivu-table-column-center"><div class="ivu-table-cell">是否有考勤</div></th>
            </tr>
            </thead>
            <tbody class="ivu-table-tbody">
            <tr v-if="attendanceData && attendanceData.length > 0" class="ivu-table-row" v-for="item in attendanceData" :key="item.id">
              <td class="ivu-table-column-center" :class="{'is-change': item.isChange}"><div class="ivu-table-cell">{{item.curriculumNum}}</div></td>
              <td class="ivu-table-column-center" :class="{'is-change': item.isChange}"><div class="ivu-table-cell">{{item.classDate}}</div></td>
              <td class="ivu-table-column-center" :class="{'is-change': item.isChange}">
                <div class="ivu-table-cell">
                  <RadioGroup v-model="item.modifyType" @on-change="changeAttendanceType(item)">
                    <Radio :label=0 :disabled="curriculumEditAble(item)">准时上课</Radio>
                    <Radio :label=1 :disabled="curriculumEditAble(item)">迟到</Radio>
                    <Radio :label=2 :disabled="curriculumEditAble(item)">缺课</Radio>
                  </RadioGroup>
                </div>
              </td>
              <td class="ivu-table-column-center" :class="{'is-change': item.isChange}"><div class="ivu-table-cell">{{item.lastModifyDate}}</div></td>
              <td class="ivu-table-column-center" :class="{'is-change': item.isChange}"><div class="ivu-table-cell">{{item.empName}}</div></td>
              <td class="ivu-table-column-center" :class="{'is-change': item.isChange}"><div class="ivu-table-cell">{{item.empModifyDate}}</div></td>
              <td class="ivu-table-column-center" :class="{'is-change': item.isChange}">
                <div class="ivu-table-cell">
                  <span v-if="item.modifyType === null" style="color: red">无</span>
                  <span v-else>有</span>
                </div>
              </td>
            </tr>
            <tr v-if="!attendanceData || !attendanceData.length">
              <td class="ivu-table-column-center" colspan="7"><div class="ivu-table-cell">暂无数据</div></td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div slot="footer">
        <Button type="primary" :disabled="!this.attendanceData.filter(a => a.isChange).length" @loading="attendLoading" @click="saveAttendance">提交</Button>
        <Button @click="editAttendanceShow = false">取消</Button>
      </div>
    </Modal>
    <!-- 课次信息 -->
    <TalSchooltime ref="talSchooltime" />
  </Layout>
</template>

<script>
import { getStrLength } from '@/utils/utils'
import BaseDataMixin from '@/mixins/baseData'
import ClassMixin from '@/mixins/classDetail'
import TeacherMixin from '@/mixins/teacherDetail'
import StudentMixin from '@/mixins/stuDetail'
import {
  queryRegisterList
} from '@/api/register'
import {
  getStudentAttendanceByRegistId,
  batchModifyAttend
} from '@/api/attendance'
import {
  queryServiceCenterByEmpNo
} from '@/api/clazz'
import { setFrom } from '@/api/bd'
import Cookie from 'js-cookie'
const defaultSetQuery = {
  cityCode: '', // 必填
  year: new Date().getFullYear() + '', // 必填
  gradeTypeId: '',
  termId: '',
  gradeId: '',
  subjectId: '',
  levelId: '', // 班次
  studentCard: '', // 学员编号
  studentName: '', // 学员姓名
  teacherId: '', // 老师id
  areaId: '',
  serviceCenterId: '',
  venueId: '', // 教学点
  classtimeTimeClassify: '', // 上课时间类型
  classtimeTimePeriod: '' // 上课时间
}
export default {
  name: 'stuAttendance',
  mixins: [BaseDataMixin, ClassMixin, TeacherMixin, StudentMixin],
  data () {
    return {
      empNo: '',
      formData: Object.assign({}, defaultSetQuery),
      loading1: false, // 教师姓名
      tableLoading: false,
      oldSearchName: '',
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数
      cityWithoutAllCity: [],
      allCityList: [], // 分校
      termList: [], // 学期
      gradeTypeList: [], // 年部
      subjectList: [], // 学科
      gradeList: [], // 年级
      levelList: [], // 班次
      teacherNameList: [], // 教师姓名
      areaList: [], // 区域
      serviceCenterList: [], // 服务中心
      venueList: [], // 教学点
      ruleForm: {
        cityCode: [{ required: true, message: '分校不能为空', trigger: 'change' }],
        year: [{ required: true, message: '年份不能为空', trigger: 'change' }],
        termId: [{ required: true, message: '学期不能为空', trigger: 'change' }],
        areaId: [{ required: true, message: '区域不能为空', trigger: 'change' }],
        serviceCenterId: [{ required: true, message: '服务中心不能为空', trigger: 'change' }]
      },
      tableData: [], // 表格数据
      tableColumns: [
        { title: '序号', type: 'index', align: 'center' },
        {
          title: '操作',
          align: 'center',
          width: '200px',
          slot: 'operation'
        },
        { title: '学员编号', key: 'studentCard', align: 'center' },
        {
          title: '学员名称',
          key: 'studentName',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  display: 'block',
                  color: '#008CF8'
                },
                on: {
                  click: () => {
                    this.showStuDetail(this.formData.cityCode, params.row.studentUid)
                  }
                }
              }, params.row.studentName)
            ])
          }
        },
        { title: '班级名称',
          key: 'className',
          align: 'center',
          width: 160,
          render: (h, params) => {
            return h('div', [
              h('div', {
                style: {
                  'text-align': 'right'
                }
              }, [
                h('Tag', {
                  style: {
                    'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                  },
                  props: {
                    color: 'green'
                  }
                }, '全国班')
              ]),
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.classId)
                  }
                }
              }, params.row.className)
            ])
          }
        },
        { title: '服务中心', key: 'serviceCenterName', align: 'center' },
        { title: '教师姓名',
          key: 'teacherNames',
          align: 'center',
          render: (h, params) => {
            const tNames = params.row.teacherNames.split(',')
            const tIds = params.row.teacherIds.split(',')
            return h('div', tNames.map((n, index) => {
              return h('a', {
                on: {
                  click: () => {
                    this.showTeacherDetail(this.formData.cityCode, tIds[index], '0')
                  }
                }
              }, n + ' ')
            }))
          }
        },
        { title: '上课时间',
          key: 'classtimeNames',
          width: '120',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: async () => {
                  this.showSchooltime({ cityId: this.formData.cityCode, id: params.row.classId })
                }
              }
            }, getStrLength(params.row.classtimeNames, 15, true))
          }
        },
        { title: '开课日期', key: 'startDate', align: 'center' },
        { title: '结课日期', key: 'endDate', align: 'center' },
        { title: '总课次', key: 'classCount', align: 'center' },
        { title: '教材领取', slot: 'isReceiptBooks', align: 'center' }
      ],
      row: {},
      attendanceCols: [
        { title: '课次', key: 'curriculumNum', align: 'center' },
        { title: '上课日期', key: 'classDate', align: 'center' },
        {
          title: '电子考勤记录',
          align: 'center',
          render: (h, params) => {
            switch (params.row.attendanceType) {
              case 0:
                return h('div', [
                  h('span', {
                    style: {
                      marginRight: '10px'
                    }
                  }, '正常'),
                  h('span', params.row.empModifyDate || params.row.attendanceTime)
                ])
              case 1:
                return h('div', [
                  h('span', {
                    style: {
                      marginRight: '10px',
                      color: 'red'
                    }
                  }, '迟到'),
                  h('span', params.row.empModifyDate || params.row.attendanceTime)
                ])
              case 2:
                return h('span', {
                  style: {
                    color: 'red'
                  }
                }, '缺勤')
              default:
                return h('span', {
                  style: {
                    color: 'red'
                  }
                }, '无数据')
            }
          }
        },
        {
          title: 'ITS考勤记录',
          align: 'center',
          render: (h, params) => {
            switch (params.row.itsAttendanceType) {
              case 0:
                return h('div', [
                  h('span', {
                    style: {
                      marginRight: '10px'
                    }
                  }, '正常'),
                  h('span', params.row.itsAttendanceTime)
                ])
              case 1:
                return h('div', [
                  h('span', {
                    style: {
                      marginRight: '10px',
                      color: 'red'
                    }
                  }, '迟到  '),
                  h('span', params.row.itsAttendanceTime)
                ])
              case 2:
                return h('span', {
                  style: {
                    color: 'red'
                  }
                }, '缺勤')
              default:
                return h('span', {
                  style: {
                    color: 'red'
                  }
                }, '无数据')
            }
          }
        },
        {
          title: '转班调课',
          align: 'center',
          render: (h, params) => {
            switch (params.row.changeType) {
              case 0:
                return h('span', {
                  style: {
                    color: '#000000'
                  }
                }, '正常')
              case 1:
                return h('span', {
                  style: {
                    color: '#008CF8'
                  }
                }, '转班')
              case 2:
                return h('span', {
                  style: {
                    color: '#008CF8'
                  }
                }, '调课')
              default:
                return h('span', '')
            }
          }
        }
      ],
      attendanceData: [],
      attendanceShow: false,
      editAttendanceShow: false,
      attendLoading: false
    }
  },
  created () {
    this.getYearListHandler()
    this.getCityListHandler()
    this.getTimeTypeListHandler()
    this.getTimeListHandler()
    this.empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
    setFrom({
      apiName: 'stuAttendancePage'
    })
  },
  watch: {
    allCityList (val) {
      if (val && val.length) {
        this.formData.cityCode = val[0].id
        this.cityYearChange(this.formData.cityCode)
      }
      this.cityWithoutAllCity = val.filter(obj => obj.id !== '1111')
    }
  },
  methods: {
    // 基本数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      let toNum = {
        cityCode: this.formData.cityCode
      }
      this.getAreaListHandler(toNum).then(res => {
        this.areaList = res
        queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: this.empNo
        }).then(data => {
          this.formData.areaId = data.areaId
          this.changeArea(data.areaId, data.serviceCenterId)
        }).catch(err => {
          this.$Message.error('获取当前所在服务中心失败:' + err.message)
        })
      }).catch(err => {
        this.$Message.error('获取区域失败:' + err.message)
      })
    },
    // 根据状态清空 学期，年部，年级，学科，班次数据
    clearList (type) {
      switch (type) {
        case 'school':
          this.termList = []
          this.formData.termId = ''
          this.gradeTypeList = []
          this.formData.gradeTypeId = ''
          this.gradeList = []
          this.formData.gradeId = ''
          this.subjectList = []
          this.formData.subjectId = ''
          this.levelList = []
          this.formData.levelId = ''
          this.formData.areaId = ''
          this.areaList = []
          this.formData.serviceCenterId = ''
          this.serviceCenterList = []
          this.formData.venueId = ''
          this.venueList = []
          break
        case 'term' :
          this.formData.gradeTypeId = ''
          this.formData.gradeId = ''
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.gradeTypeList = []
          this.gradeList = []
          this.subjectList = []
          this.levelList = []
          break
        case 'gradeType' :
          this.formData.gradeId = ''
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.gradeList = []
          this.subjectList = []
          this.levelList = []
          break
        case 'grade' :
          this.formData.subjectId = ''
          this.formData.levelId = ''
          this.subjectList = []
          this.levelList = []
          break
        case 'subject' :
          this.formData.levelId = ''
          this.levelList = []
          break
      }
    },
    cityYearChange (val) { // 改变城市
      this.clearList('school')
      if (!val) {
        return false
      }
      if (!this.formData.cityCode) {
        this.$Message.warning('请选择城市')
        return false
      }
      this.getBaseData()
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      this.getLinkageHandler(req).then(res => {
        this.termList = res
      }).catch(err => {
        this.$Message.error('获取学期失败:' + err.message)
      })
    },
    termChange (val) { // 改变学期
      this.clearList('term')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeTypeList = res
      }).catch(err => {
        this.$Message.error('获取年部失败:' + err.message)
      })
    },
    gradeTypeChange (val) { // 改变年部
      this.clearList('gradeType')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error('获取年级失败:' + err.message)
      })
    },
    gradeChange (val) { // 改变年级
      this.clearList('grade')
      if (!val) return
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      this.getLinkageHandler(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error('获取学科失败:' + err.message)
      })
    },
    subjectChange (val) { // 改变学科
      this.clearList('subject')
      if (!val) return
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        subjectId: this.formData.subjectId,
        queryType: 'c'
      }
      this.getLinkageHandler(req).then(res => {
        this.levelList = res
      }).catch(err => {
        this.$Message.error('获取班次失败:' + err.message)
      })
    },
    // 主讲老师搜索
    getTeacher (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        this.teacherNameList = res.records
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },
    clearTeacher () {
      this.oldSearchName = ''
      if (this.formData.teacherId) {
        this.formData.teacherId = ''
      }
    },
    changeArea (val, serviceCenterId) { // 改变区域
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req).then(res => {
        this.serviceCenterList = res
        this.serviceCenterList.forEach((value) => {
          if (!value.address) {
            value.address = ''
          }
        })
        if (serviceCenterId) {
          this.formData.serviceCenterId = serviceCenterId
          this.changeService(serviceCenterId)
        }
      }).catch(err => {
        this.$Message.warning('获取服务中心失败:' + err.message)
      })
    },
    changeService (val) { // 改变服务中心
      this.venueList = []
      this.formData.venueId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning('获取教学点失败:' + err.message)
      })
    },
    // 查询
    search (data) {
      this.currentPageNum = 1
      this.getTableData(data)
    },
    // 重置
    resetField () {
      this.formData = Object.assign({}, defaultSetQuery)
      if (this.cityWithoutAllCity) {
        this.formData.cityCode = this.cityWithoutAllCity[0].id
        this.cityYearChange(this.formData.cityCode)
      }
      this.$refs['form'].resetFields()
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData('form')
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData('form')
    },
    // 表格数据
    getTableData (data) {
      this.$refs[data].validate((valid) => {
        if (valid) {
          this.formData.pageSize = this.currentPageSize
          this.formData.pageNo = this.currentPageNum
          this.tableLoading = true
          queryRegisterList(this.formData).then(res => {
            if (res.records.length === 0) {
              this.tableData = []
              this.$Message.warning('暂无数据')
            }
            this.pageTotal = res.totalCount
            this.tableData = res.records
            this.tableLoading = false
          }).catch(err => {
            this.tableLoading = false
            this.pageTotal = 0
            this.tableData = []
            this.currentPageNum = 1
            this.currentPageSize = 10
            this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
          })
        }
      })
    },
    showAttendance (row, edit) {
      this.row = row
      getStudentAttendanceByRegistId({
        cityCode: this.formData.cityCode,
        registId: row.id
      }).then(data => {
        this.attendanceData = data
        if (edit) {
          this.editAttendanceShow = true
        } else {
          this.attendanceShow = true
        }
      }).catch(err => {
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      })
    },
    changeAttendanceType (row) {
      this.$set(row, 'isChange', true)
    },
    saveAttendance () {
      const params = this.attendanceData.filter(a => a.isChange).map(a => {
        return {
          attendanceType: a.modifyType,
          curriculumId: a.curriculumId,
          studentId: this.row.studentId
        }
      })
      this.attendLoading = true
      batchModifyAttend({
        cityCode: this.formData.cityCode,
        attendanceList: params
      }).then(() => {
        this.$Message.success({
          content: '修改考勤成功',
          duration: 5
        })
        this.editAttendanceShow = false
      }).catch(err => {
        this.$Message.error({
          content: '修改考勤失败：' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.attendLoading = false
      })
    },
    curriculumEditAble (row) {
      return new Date().getTime() < new Date(row.classDate).getTime() || row.modifyType === null
    }
  }
}
</script>
<style lang="less" scoped>
  #TalClassAttendance {
    width: 100%;
    overflow-x: hidden;
    padding: 10px;
  }
  .container {
    width: 100%;
    padding-left: 20px;
    padding-right: 24px;
  }

  .class_top {
    background: #fff;
    padding-top: 24px;
    font-size: 14px;
  }
  .is-change {
    background: #fde5cd;
  }
</style>
