<template>
  <div class="wrapper">
    <div v-for="(item, index) in dataList" :key="index" class="class">
      <div style="text-align: right">打印时间：{{ now }}</div>
      <div class="class-name">{{item.className}}</div>
      <div class="class-info">
        <div class="info">课次：第{{item.curriculumNo}}次课</div>
        <div class="info">服务中心：{{item.serviceCenterName}}</div>
        <div class="info">教学点：{{item.venueName}}</div>
        <div class="info">教室：{{item.classroomName}}</div>
        <div class="info">主讲老师：{{item.teacherNames}}</div>
        <div class="info">开课时间：{{item.startDate ? item.startDate.split(' ')[0] : ''}} 至 {{item.endDate ? item.endDate.split(' ')[0] : ''}}</div>
        <div class="info">上课时间：{{item.classtimeNames}} <span>({{item.classDate}})</span> </div>
        <div class="info">教师签字：</div>
      </div>
      <table border="1" width="100%" cellspacing="0" cellpadding="0" bgcolor="#ffffff" bordercolordark="#ffffff" bordercolorlight="#000000" style="border:1px solid #000;border-collapse:collapse">
        <tr>
          <td colspan="3" align="center">上课学员名单</td>
        </tr>
        <tr>
          <td align="center" width="60px">编号</td>
          <td align="center" width="100px">学员姓名</td>
          <td align="center">考勤</td>
        </tr>
        <tr v-for="(stuName, index2) in item.studentNameList" :key="index2">
          <td align="center">{{index2 + 1}}</td>
          <td align="center">{{stuName}}</td>
          <td align="center"></td>
        </tr>
      </table>
      <div class="page-break" v-if="dataList.length != index + 1"></div>
    </div>
  </div>
</template>

<script>
import { getCurriculumRollCallingPaper } from '@/api/clazz'
import { dateFormat } from '@/utils/utils'
export default {
  data () {
    return {
      dataList: [],
      now: dateFormat(new Date())
    }
  },
  mounted () {
    document.documentElement.className = 'printPage'
    document.body.className = 'printPage'
    let params = {
      cityCode: this.$route.query.cityCode,
      classIds: this.$route.query.classIds,
      lessonNum: this.$route.query.curriculumId
    }
    getCurriculumRollCallingPaper(params).then(data => {
      if (data && data.length) {
        this.dataList = data
        setTimeout(() => {
          window.print()
        }, 500)
      } else {
        this.dataList = []
      }
    }).catch(err => {
      this.$Message.error({ content: '获取点名册信息失败：' + err.message, duration: 5 })
    })
  }
}
</script>
<style lang="less">
  .printPage {
    height: initial;
    overflow: auto!important;
  }
</style>
<style lang="less" scoped>
  .wrapper{
    /*padding-left: 20px;*/
    /*padding-right: 20px;*/
    .class {
      margin-bottom: 30px;
      .class-name {
        text-align: center;
        font-size: 16px;
        font-weight: bold;
      }
      .class-info {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10px;
        .info {
          width: 50%;
          margin-top: 10px;
        }
      }
    }
    .page-break {
      page-break-after: always;
    }
  }
</style>
