        },
        {
          title: '调课',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isChangeCourse === 0 ? '否' : '是')
          }
        },
        {
          title: '转班',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isChangeClass === 0 ? '否' : '是')
          }
        }
      ],
      editAttendModal: false,
      editAttendCols: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        { title: '学员姓名', key: 'studentRealName', align: 'center' },
        { title: '学员编号', key: 'studentCard', align: 'center' },
        {
          title: '考勤状态',
          align: 'center',
          width: '200px',
          slot: 'attendanceType'
        },
        {
          title: '考勤时间',
          align: 'center',
          key: 'attendanceTime'
        },
        {
          title: '最后修改人',
          align: 'center',
          key: 'empName'
        },
        {
          title: '修改时间',
          align: 'center',
          key: 'empModifyDate'
        },
        {
          title: '修改时间',
          align: 'center',
          slot: 'hasAttendance'
        }
      ],
      attendData: [],
      row: {},
      selectedRows: [],
      selectedCurriculums: []
    }
  },
  methods: {
    showCurriculum (row) {
      this.row = row
      this.curriculumModal = true
      this.curriculumLoading = true
      getCurriculumList({
        cityCode: row.cityCode,
        classId: row.id
      }).then(data => {
        this.curriculumData = data.sort((d1, d2) => Number(d1.classNum) - Number(d2.classNum))
      }).catch(err => {
        this.curriculumData = []
        this.$Message.error({
          content: `获取课次信息失败：${err.message}`,
          duration: 5
        })
        this.curriculumModal = false
      }).finally(() => {
        this.curriculumLoading = false
      })
    },
    showAttendance (id, edit) {
      this.attendLoading = true
      this.curriculumId = id
      getAttendanceByCurriculumId({
        cityCode: this.row.cityCode,
        curriculumId: id
      }).then(data => {
        this.attendData = data
        if (edit) {
          this.editAttendModal = true
        } else {
          this.selectedCurriculums = []
          this.attendModal = true
        }
      }).catch(err => {
        this.attendData = []
        this.$Message.error({
          content: `获取班次考勤信息失败：${err.message}`,
          duration: 5
        })
        this.attendModal = false
      }).finally(() => {
        this.attendLoading = false
      })
    },
    // 授课类型
    getClassTypeName (val) {
      return this.classTypeMap[val]
    },
    search () {
      this.currentPageNum = 1
      this.getTableData()
    },
    getTableData () {
      if (!this.formData.cityCode) {
        this.$Message.warning('请选择所在城市')
        return
      }
      if (!this.formData.year) {
        this.$Message.warning('请选择年份')
        return
      }
      if (!this.formData.termId) {
        this.$Message.warning('请选择学期')
        return
      }
      this.formData.pageSize = this.currentPageSize
      this.formData.pageNo = this.currentPageNum
      this.formData.passCountEnd = this.formData.passCountStart
      this.tableLoading = true
      queryTeacherAttendanceClassList(this.formData).then(res => {
        this.selectedRows = []
        if (res.records.length === 0) {
          this.tableData = []
          this.$Message.warning('暂无数据')
        }
        this.pageTotal = res.totalCount
        this.studentDetail = {
          cityCode: this.formData.cityCode
        }
        this.tableData = res.records
        this.tableLoading = false
      }).catch(err => {
        this.tableData = []
        this.pageTotal = 0
        this.currentPageSize = 10
        this.currentPageNum = 1
        this.tableLoading = false
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      })
    },
    // 基本数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      // cityCode
      let toNum = {
        cityCode: this.formData.cityCode
      }
      this.getAreaListHandler(toNum).then(res => {
        this.areaList = res
        queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: this.$store.state.user.userInfo.id
        }).then(data => {
          this.formData.areaId = data.areaId
          this.changeArea(data.areaId, data.serviceCenterId)
        }).catch(err => {
          this.$Message.error('获取当前所在服务中心失败:' + err.message)
        })
      }).catch(err => {
        this.$Message.error('获取区域失败:' + err.message)
      })
      // this.getTimeTypeListHandler()
      // this.getTimeListHandler()
    },
    cityYearChange (val) { // 改变城市
      this.formData.termId = ''
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectIds = ''
      this.formData.levelId = ''
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.formData.venueId = ''
      this.formData.classtimeTimePeriod = ''
      this.formData.classtimeTimeClassify = ''
      this.termList = []
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      this.areaList = []
      this.serviceCenterList = []
      this.venueList = []
      // this.classtimeList = []
      if (!val) {
        return false
      }

      if (!this.formData.cityCode) {
        this.$Message.warning('请选择城市')
        return false
      }

      this.getBaseData()
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      this.getLinkageHandler(req).then(res => {
        this.termList = res
      }).catch(err => {
        this.$Message.error('获取学期失败:' + err.message)
      })
    },
    termChange (val) { // 改变学期
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectIds = ''
      this.formData.levelId = ''
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeTypeList = res
      }).catch(err => {
        this.$Message.error('获取年部失败:' + err.message)
      })
    },
    gradeTypeChange (val) { // 改变年部
      this.formData.gradeId = ''
      this.formData.subjectIds = ''
      this.formData.levelId = ''
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error('获取年级失败:' + err.message)
      })
    },
    gradeChange (val) { // 改变年级
      this.formData.subjectIds = ''
      this.formData.levelId = ''
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      this.getLinkageHandler(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error('获取学科失败:' + err.message)
      })
    },
    subjectChange (val) { // 改变学科
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        subjectIds: this.formData.subjectIds,
        queryType: 'c'
      }
      this.getLinkageHandler(req).then(res => {
        this.levelList = res
      }).catch(err => {
        this.$Message.error('获取班次失败:' + err.message)
      })
    },
    changeArea (val, serviceCenterId) { // 改变区域
      this.serviceCenterList = []
      this.formData.service = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req).then(res => {
        this.serviceCenterList = res
        this.serviceCenterList.forEach((value) => {
          if (!value.address) {
            value.address = ''
          }
        })
        if (serviceCenterId) {
          this.formData.serviceCenterId = serviceCenterId
          this.changeService(serviceCenterId)
        }
      }).catch(err => {
        this.$Message.warning('获取服务中心失败:' + err.message)
      })
    },
    changeService (val) { // 改变服务中心
      this.venueList = []
      this.formData.venueId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning('获取教学点失败:' + err.message)
      })
    },
    // 主讲老师搜索
    getTeacher (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        this.teacherNameList = res.records
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    getTutor (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.tutorNameList = []
      let req = {
        sysName: query,
        teacherType: 1,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        // this.tutorNameList = res.records  TODO @@@
        this.tutorNameList = res.records
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取辅导老师姓名失败:' + err.message)
      })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },
    // 表单重置
    resetFields () {
      depClone(this.formData, this.formData2)
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    selectionChange (selection) {
      this.selectedRows = selection
    },
    selectionCurriculumChange (selection) {
      this.selectedCurriculums = selection
    },
    // 开课日期、结课日期改变
    startDateChange (val) {
      if (this.formData.endDate) {
        if (new Date(val).getTime() > new Date(this.formData.endDate).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData.startDate = ''
          this.formData.startDateVal = ''
          return
        }
      }
      this.formData.startDate = val
    },
    endDateChange (val) {
      if (this.formData.startDate) {
        if (new Date(this.formData.startDate).getTime() > new Date(val).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData.endDate = ''
          this.formData.endDateVal = ''
          return
        }
      }
      this.formData.endDate = val
    },
    /**
     * 打印选中班级学员点名册
     */
    printStudentNames () {
      top.open(`/service/printStudentNames?cityCode=${this.formData.cityCode}&classIds=${this.selectedRows.map(c => c.id).join(',')}&print=true`)
    },
    /**
     * 打印选中班当次课考勤名单
     */
    printCurrentCurriculumAttendance () {
      if (!this.formData.passCountStart || !this.formData.passCountEnd) {
        this.$Message.warning({
          content: '请输入大于0的已上课次并进行搜索',
          duration: 5
        })
        return
      }
      top.open(`/service/printCurriculumAttendance?cityCode=${this.formData.cityCode}&classIds=${this.selectedRows.map(c => c.id).join(',')}&classNum=${this.formData.passCountStart}&print=true`)
    },
    /**
     * 按课次打印点名册
     */
    printOneCurriculumStudentNames () {
      this.printCurriculum = null
      this.confirmCurriculumModal = true
    },
    /**
     * 单课次打印点名册
     */
    confirmPrintStuNames () {
      const invalidRows = this.selectedRows.filter(r => r.classCount < this.printCurriculum)
      if (invalidRows.length > 0) {
        this.$Message.warning({
          content: `您所选择的班级 ${invalidRows.map(r => '【' + r.name + '】').join('、')} 总课次小于要打印的课次，请重新输入课次`,
          duration: 5
        })
        return
      }
      top.open(`/service/printOneCurriculumStudentNames?cityCode=${this.formData.cityCode}&classIds=${this.selectedRows.map(c => c.id).join(',')}&curriculumId=${this.printCurriculum}&print=true`)
      this.confirmCurriculumModal = false
    },
    /**
     * 打印选中项考勤单
     */
    printSelectedCurriculumAttendance () {
      top.open(`/service/printCurriculumAttendance?cityCode=${this.formData.cityCode}&classIds=${this.row.id}&curriculumId=${this.selectedCurriculums.map(c => c.id).join(',')}&print=true`)
    },
    /**
     * 打印考勤单
     */
    printCurriculumAttendance (curriculumId) {
      top.open(`/service/printCurriculumAttendance?cityCode=${this.formData.cityCode}&classIds=${this.row.id}&curriculumId=${curriculumId}&print=true`)
    },
    changeAttendanceType (row) {
      this.$set(row, 'isChange', true)
    },
    saveAttendance () {
      const params = this.attendData.filter(a => a.isChange).map(a => {
        return {
          attendanceType: a.modifyType,
          curriculumId: this.curriculumId,
          studentId: a.studentId
        }
      })
      this.attendLoading = true
      batchModifyAttend({
        cityCode: this.formData.cityCode,
        attendanceList: params
      }).then(() => {
        this.$Message.success({
          content: '修改考勤成功',
          duration: 5
        })
        this.editAttendModal = false
      }).catch(err => {
        this.$Message.error({
          content: '修改考勤失败：' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.attendLoading = false
      })
    },
    curriculumSelectable (row) {
      // new Date('2021-06-17') 取的是GMT东八区北京早上8点，应取标准GMT0点时间
      const classDate = `${row.classDate} 00:00:00`
      return new Date().getTime() > new Date(classDate).getTime()
    }
  },
  watch: {
    allCityList (val) {
      if (val.length === 1) {
        this.formData.cityCode = val[0].id
        this.formData2.cityCode = val[0].id
        this.cityYearChange(this.formData.cityCode)
      }
    }
  },
  mounted () {
    this.getYearListHandler()
    this.getCityListHandler()
    this.getClassTypeHandler()
    this.getTimeTypeListHandler()
    this.getTimeListHandler()
    setFrom({
      apiName: 'classAttendancePage'
    })
  }
}
</script>

<style scoped lang="less">
  #TalClassAttendance {
    overflow-x: hidden;
    padding: 10px;
  }
  .class_top {
    background: #fff;
    padding-top: 24px;
    padding-bottom: 10px;
    font-size: 14px;
  }
  .info {
    display: flex;
    margin-bottom: 10px;
    span {
      margin-right: 50px;
    }
  }
  .is-change {
    background: #fde5cd;
  }

</style>
