<template>
  <Layout id="TalAttendanceCardMgt" class="gary">
    <!--<TalSearchApplyBase :show-detail="false" @stuInfo="setStuInfo"></TalSearchApplyBase>-->
    <TalStuSearch
      ref="stuSearch"
      :show-detail="false"
      :stu-card="queryData.card"
      :stu-city-code="queryData.cityCode"
      @writeSuccess="writeCardSuccess"
      @stuInfo="setStuInfo"></TalStuSearch>
      <Table style="width: 100%;margin-top: 10px" :loading="tableLoading" :columns="cols" :data="tableData">
      <template slot-scope="{ row }"  slot="operation">
        <a v-if="row.oldCardNum" @click="openCardModal(1)" style="margin-right: 5px">换卡</a>
        <a v-else @click='openCardModal' style="margin-right: 5px">绑定</a>
        <a @click='openPrintModal' style="margin-right: 5px">打印学员信息</a>
        <a @click="showLog">日志</a>
      </template>
      <template slot-scope="{ row }"  slot="status">
        {{row.oldCardNum ? '已绑定' : '未绑定'}}
      </template>
      <template slot-scope="{ row }"  slot="sexName">
        {{row.sex === 0 || row.sex === 2 ? '女' : '男'}}
      </template>
    </Table>
    <Modal
      :mask-closable="false"
      :title="change ? '换卡' : '绑定新卡'"
      v-model="cardModal"
      width="400px">
      <div>
        <div>
          <span style="margin-right: 50px">学员姓名：{{studentDetail.realName}}</span><span>学员编号：{{studentDetail.card}}</span>
        </div>
        <div v-if="change" style="margin-top: 30px">
          <div>卡类型：{{studentDetail.attendanceCardStyle}}</div>
          <div style="margin-top: 10px">卡编号：{{studentDetail.oldCardNum}}</div>
        </div>
        <Form :model="formData" :rules="formValidate" style="margin-top: 30px" ref="form">
          <FormItem label="卡类型" required prop="cardTypeId">
            <Select v-model="formData.cardTypeId" @on-change="cardTypeChange" style="width: 200px">
              <Option v-for="item in cardTypes" :value="item.typeId" :key="item.typeId">{{item.typeName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="卡编号" required prop="cardNo">
            <Input v-model="formData.cardNo" style="width: 200px" disabled/>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button v-if="change" @click="writeCard" type="primary">换卡</Button>
        <Button v-else @click="writeCard" type="primary">绑卡</Button>
      </div>
    </Modal>
    <Modal
      :mask-closable="false"
      title="打印卡片"
      v-model="printModal"
      width="700px">
      <div>
        <div style="margin-bottom: 10px">
          <h4 style="margin-bottom: 10px">学员信息</h4>
          <div>学员姓名：{{studentDetail.realName}}</div>
          <div>学员编号：{{studentDetail.card}}</div>
        </div>
        <div>
          <h4 style="margin-bottom: 10px">请选择打印卡片样式</h4>
          <RadioGroup v-model="styleIndex">
            <Radio :label=0><img width="300" height="100" src="../../../assets/images/WechatIMG286.jpeg"></Radio>
            <Radio :label=1><img width="300" height="100" src="../../../assets/images/WechatIMG285.png"></Radio>
            <Radio :label=2><img width="300" height="100" src="../../../assets/images/WechatIMG288.png"></Radio>
            <Radio :label=3><img width="300" height="100" src="../../../assets/images/WechatIMG292.png"></Radio>
          </RadioGroup>
        </div>
      </div>
      <div slot="footer">
        <Button @click="printCard" type="primary" style="margin-right: 10px">打印</Button>
      </div>
    </Modal>
    <Modal
      :mask-closable="false"
      title="操作记录"
      v-model="logModal"
      width="960px">
      <Table stripe :columns="logColumns" :data="logData" style="margin-top: 20px"></Table>
      <div slot="footer">
        <Button @click="closeLogModal">关闭</Button>
      </div>
    </Modal>
  </Layout>
</template>

<script>
import { browser } from '../../../utils/utils'
import {
  getAllCardType,
  getUnUsedAttendanceCard,
  bindAttendanceCard,
  changeAttendanceCard
} from '@/api/attendance'
import {
  getStudentDetail
} from '@/api/student'
import { setFrom, findOperationLogs } from '@/api/bd'
import getUserInfoByUidMixin from '@/components/mixin/getUserInfoByUid'
export default {
  name: 'attendanceCardMgt',
  mixins: [ getUserInfoByUidMixin ],
  data () {
    return {
      styleIndex: 0,
      styleConfig: [
        {
          m_name_config: '635,434,850,500,6',
          m_cardnum_config: '630,474,850,520,6',
          m_img_config: '500,120,190,190'
        },
        {
          m_name_config: '500,200,850,500,14',
          m_cardnum_config: '500,270,850,520,8',
          m_img_config: '500,120,190,190'
        },
        {
          m_name_config: '500,200,850,500,14',
          m_cardnum_config: '500,270,850,520,8',
          m_img_config: '500,120,190,190'
        },
        {
          m_name_config: '60,200,850,500,14',
          m_cardnum_config: '60,270,850,520,8',
          m_img_config: '500,120,190,190'
        }
      ],
      cols: Object.freeze([
        { title: '操作', align: 'center', slot: 'operation' },
        { title: '学员姓名', align: 'center', key: 'realName' },
        { title: '学员编号', align: 'center', key: 'card' },
        { title: '性别', align: 'center', slot: 'sexName' },
        { title: '年级', align: 'center', key: 'gradeName' },
        { title: '状态', align: 'center', slot: 'status' },
        { title: '考勤卡编号', align: 'center', key: 'oldCardNum' }

      ]),
      studentDetail: {},
      tableData: [],
      tableLoading: false,
      cardModal: false, // 写卡模态窗口
      change: false, // 是否换卡操作
      cardTypes: [],
      formData: {
        cardNo: '',
        cardTypeId: ''
      },
      printModal: false,
      formValidate: {
        cardTypeId: [
          { required: true, message: '请选择', trigger: 'change', type: 'string' }
        ],
        cardNo: [
          { required: true, message: '无卡号', trigger: 'blur', type: 'string' }
        ]
      },
      logModal: false,
      logColumns: Object.freeze([
        {
          title: '日期',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '操作人',
          key: 'empName',
          align: 'center'
        }, {
          title: '操作',
          key: 'operateOverview',
          align: 'center'
        }, {
          title: '考勤卡编号',
          key: 'businessId',
          align: 'center'
        }
      ]),
      logData: [],
      printerWS: null,
      isPrinterWSOpen: 0,
      wsCount: 0
    }
  },
  methods: {
    showLog () {
      findOperationLogs({
        cityCode: this.studentDetail.cityCode,
        uid: this.studentDetail.uid,
        operateType: 'attendance'
      }).then((res) => {
        this.logModal = true
        if (res && res.length > 0) {
          this.logData = res
        } else {
          this.logData = []
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      })
    },
    closeLogModal () {
      this.logModal = false
      this.logData = []
    },
    setStuInfo (stuInfo) {
      this.tableData = [stuInfo]
      this.studentDetail = stuInfo
      this.getCardType()
    },
    openCardModal (change) {
      this.cardModal = true
      this.change = change === 1
      this.formData.cardTypeId = ''
      this.formData.cardNo = ''
    },
    writeCard () {
      try {
        this.$refs['form'].validate(valid => {
          if (valid) {
            // if (browser().ie) {
            //   document.Card.WriteCard(this.formData.cardNo)
            // } else {
            this.$refs.stuSearch.sendMsgToCard(this.formData.cardNo, 2)
            // }
          }
        })
      } catch (e) {
        console.log('写卡异常')
        console.error(e)
      }
    },
    writeCardSuccess () {
      if (this.change) {
        changeAttendanceCard({
          cityCode: this.studentDetail.cityCode,
          stuId: this.studentDetail.studentId,
          uid: this.studentDetail.uid,
          stuUid: this.studentDetail.uid,
          typeId: this.formData.cardTypeId,
          typeName: this.cardTypes.filter(r => r.typeId === this.formData.cardTypeId)[0].typeName,
          cardNum: this.formData.cardNo,
          oldCardNum: this.studentDetail.oldCardNum
        }).then(data => {
          this.$Modal.success({
            title: '提示',
            content: '考勤卡换卡成功',
            onOk: () => {
              this.cardModal = false
            }
          })
          this.update()
        }).catch(e => {
          this.$Modal.error({
            title: '提示',
            content: `考勤卡换卡失败：${e.message}，请重试`
          })
        })
      } else {
        bindAttendanceCard({
          cityCode: this.studentDetail.cityCode,
          stuId: this.studentDetail.studentId,
          uid: this.studentDetail.uid,
          stuUid: this.studentDetail.uid,
          typeId: this.formData.cardTypeId,
          typeName: this.cardTypes.filter(r => r.typeId === this.formData.cardTypeId)[0].typeName,
          cardNum: this.formData.cardNo
        }).then(data => {
          this.$Modal.success({
            title: '提示',
            content: '考勤卡绑定成功',
            onOk: () => {
              this.cardModal = false
            }
          })
          this.update()
        }).catch(e => {
          this.$Modal.error({
            title: '提示',
            content: `考勤卡绑定失败：${e.message}，请重试`
          })
        })
      }
    },
    openPrintModal () {
      this.printModal = true
    },

    printCard () {
      const config = this.styleConfig[this.styleIndex]
      console.log(config)
      const str_content = JSON.stringify({ Name_Config: config.m_name_config, Name: this.studentDetail.realName, CardNum_Config: config.m_cardnum_config, CardNum: this.studentDetail.card, Img_Config: config.m_img_config, ImgUrl: '' })
      this.sendPrintCardMsg(str_content)
    },
    // printCard2 () {
    //   const name = this.studentDetail.realName
    //   const cardNo = this.studentDetail.card
    //   // const cardNo = '000000000000000000'
    //   const imgUrl = ''
    //   const m_name_config = '500,200,850,500,14'
    //   const m_name = name
    //   const m_cardnum_config = '500,270,850,520,8'
    //   const m_cardnum = cardNo
    //   const m_img_config = '500,120,190,190'
    //   const m_imgurl = imgUrl
    //   const str_content = JSON.stringify({ Name_Config: m_name_config, Name: m_name, CardNum_Config: m_cardnum_config, CardNum: m_cardnum, Img_Config: m_img_config, ImgUrl: m_imgurl })
    //   this.sendPrintCardMsg(str_content)
    // },
    // valMessage (rs) {
    //   try {
    //     var json = eval('(' + rs + ')')
    //     if (json.jumpType) {
    //       return true
    //     }
    //   } catch (e) {
    //     this.$Modal.error({
    //       title: '提示',
    //       content: `考勤卡绑定失败：${e.message}，请重试`
    //     })
    //     return false
    //   }
    //   this.$Modal.error({
    //     title: '提示',
    //     content: `考勤卡绑定失败，请重试`
    //   })
    //   return false
    // },
    getCardType () {
      getAllCardType({
        cityCode: this.studentDetail.cityCode
      }).then(data => {
        this.cardTypes = [ data ]
      }).catch(err => {
        this.$Message.error({
          content: `获取卡类型失败：${err.message}`,
          duration: 5
        })
      })
    },
    cardTypeChange (val) {
      if (val) {
        getUnUsedAttendanceCard({
          cityCode: this.studentDetail.cityCode,
          typeId: val
        }).then(date => {
          this.formData.cardNo = date.cardNum
        }).catch(err => {
          this.$Message.error({
            content: `获取未使用考勤卡号失败：${err.message}`,
            duration: 5
          })
        })
      }
    },
    update () {
      this.tableLoading = true
      setTimeout(() => {
        getStudentDetail({
          cityCode: this.studentDetail.cityCode,
          uid: this.studentDetail.uid
        }).then((res) => {
          this.studentDetail = res
          this.tableData = [ res ]
        }).catch((err) => {
          this.$Message.warning({ content: err.message, duration: 5 })
        }).finally(() => {
          this.tableLoading = false
        })
      }, 2000)
    },
    // saveData (nowtime) {
    //   if (this.valMessage(nowtime)) {
    //     this.writeCardSuccess()
    //   }
    // },
    initPrinterWS () {
      let host = browser().ie ? '127.0.0.1' : 'localhost'
      this.printerWS = new WebSocket(`wss://${host}:11311/Printer`)
      this.printerWS.onopen = e => {
        this.isPrinterWSOpen = 1
        console.log('Openened connection to websocket')
        console.log(e)
      }
      this.printerWS.onclose = () => {
        console.log('Close connection to websocket')
        this.isPrinterWSOpen = 0
        this.wsCount += 1
        // 断线重连
        if (this.wsCount < 10) {
          this.initPrinterWS()
        }
      }
      this.printerWS.onmessage = e => {
        const data = JSON.parse(e.data)
        switch (data.command) {
          case 3:
            if (data.result === 1) {
              this.$Message.success({ content: '打印成功', duration: 5 })
              this.printModal = false
            } else {
              this.$Message.error({ content: '打印失败：' + data.content, duration: 5 })
            }
            break
          default:
            this.$Message.error({ content: '打印错误：' + data.content, duration: 5 })
            break
        }
      }
    },
    sendPrintCardMsg (str_content) {
      this.printerWS.send(JSON.stringify({ content: str_content, command: 3, order: -1 }))
    }
  },
  mounted () {
    setFrom({
      apiName: 'attendanceManagePage'
    })
    // if (browser().ie) {
    //   try {
    //     // 将CallByActiveX方法绑定到window下面，提供写卡插件使用
    //     window['CallByActiveX'] = (nowtime) => {
    //       this.saveData(nowtime)
    //     }
    //   } catch (e) {
    //     console.error(e)
    //   }
    // } else {
    this.initPrinterWS()
    // }
  }
}
</script>

<style scoped>
  #TalAttendanceCardMgt {
    overflow-x: hidden;
    padding: 10px;
  }
</style>
